body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background: #1b1b61;
  min-height: 100vh;
}

.principal::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  background: linear-gradient(#711111, #5b00ff);
  clip-path: circle(30% at right top);
}
.principal::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  background: linear-gradient(#008eff, #e91eba);
  clip-path: circle(20% at left bottom);
}

.container {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 5rem 0;
}

.card {
  position: relative;
  width: 300px;
  height: 400px;
  background: rgba(255, 255, 255, 0.05);
  margin: 2rem;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}
.card:hover .content {
  opacity: 1;
  transform: translateY(-20px);
}
.card:hover .sci li {
  transform: translateY(20px);
  opacity: 1;
}
.card:hover .contentBx h3 span {
  color: rgba(255, 255, 255, 0.7);
}
.card .content {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0.5;
  transition: 0.5s;
}
.card .imgBx {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  border: 10px solid rgba(0, 0, 0, 0.25);
}
.card .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contentBx h3 {
  color: #FFF;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 2.4rem;
  text-align: center;
  margin: 20px 0 10px;
  line-height: 1.1em;
}
.contentBx h3 span {
  font-size: 1.8rem;
  font-weight: 300;
  text-transform: initial;
}

.sci {
  position: absolute;
  bottom: -10%;
  display: flex;
}
.sci li {
  margin: 0 10px;
  transform: translateY(40px);
  transition: 0.5s;
  opacity: 0;
  transition-delay: calc(0.1s * var(--i));
}
.sci li a {
  color: #FFF;
  font-size: 24px;
}